// pages/index.tsx
import * as React from "react";
import { Helmet } from "react-helmet-async";

import Header from "../Components/header";
import MobileStickyCTA from "../Components/MobileStickyCTA";
import HeroSection from "../Components/hero";
import TestimonialSlider from "../Components/testimonial";
import ServiceCard from "../Components/Services";
import NewsletterSubscription from "../Components/NewsletterSubscription";
import Footer from "../Components/Footer";
import FAQSection from "../Components/Faq";
import ProjectManagementSection from "../Components/procces";
import AboutSection from "../Components/about";
import RecentProjects from "../Components/projects";
import ExitIntentPopup from "../Components/Exit";
import WarrantyBadgeSection from "../Components/WarrantyBadgeSection";
import { HelmetProvider } from 'react-helmet-async';
const IndexPage = () => {
  return (
    <main>
      <HelmetProvider>
        <title>Bravo Roofing - Premium Roofing Solutions for Every Space</title>
        <meta
          property="og:title"
          content="Bravo Roofing - Premium Roofing Solutions for Every Space"
        />
        <meta
          property="og:description"
          content="Explore premium roofing options for your home or business."
        />
        <meta
          property="og:image"
          content="https://preview.bravoroofingidaho.com/social-share.png"
        />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta
          property="og:url"
          content="https://preview.bravoroofingidaho.com"
        />
        <meta property="og:type" content="article" />
        <meta name="twitter:card" content="summary_large_image" />
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "Product",
              "name": "Bravo Roofing - Premium Roofing Solutions for Every Space",
              "image": "https://preview.bravoroofingidaho.com/social-share.png",
              "description": "Explore premium flooring options for your home or business. Visit our showroom or request a free consultation today.",
              "brand": {
                "@type": "Brand",
                "name": "Bravo Roofing"
              },
              "url": "https://preview.bravoroofingidaho.com"
            }
          `}
        </script>
      </HelmetProvider>

      <ExitIntentPopup />
      <Header />
      <HeroSection />
      <WarrantyBadgeSection />
      <TestimonialSlider />
      <ServiceCard />
      <AboutSection />
      <ProjectManagementSection />
      <FAQSection />
      <RecentProjects />
      <Footer />
    </main>
  );
};

export default IndexPage;
